  }

  .search__form.active {
    z-index: 1;
    display: none;
  }

  .theme-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 30px;
    left: 213px;
  }

  .navigation-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 114px;
    left: 326px;
  }

